<template>
  <div class="popup" :class="{active:showPopup}" @click="handleMask">
    <transition name="fade">
      <div class="popup-container" :class="{active:showPopup}">
        <div class="top f_ai">
          <span></span>
          <span>({{playList.length}})</span>
          <span class="iconfont icon-ms" @click.stop="showMode"></span>
        </div>
        <div class="bottom">
          <div class="content f_ai" v-for="(item,index) in playList" :key="item.sound.id">
            <div class="left">
              <!--  -->
              <span class="iconfont" :class="{'icon-yinle':(id===item.sound.id)}"></span>
              <span
                class="name ellipsis"
                @click="changeMusic(item.sound.id,index,item)"
              >{{item.sound.name}}</span>
            </div>
            <div class="right iconfont icon-sc" @click.stop="removeSong(item.sound.id)"></div>
          </div>
        </div>

        <div class="mode" :class="{active:showModeList}">
          <div class="modeList">
            <div
              class="mode-item f_ai"
              :class="item.mode===Mode?'active':''"
              v-for="(item,index) in modeList"
              :key="index"
              @click.stop="changeMode(index)"
            >
              <span>{{item.name}}</span>
              <span class="iconfont" :class="item.icon"></span>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  mounted() {},
  data() {
    return {
      modeList: [
        { name: "", icon: "icon-ms", mode: "listCircle" },
        { name: "", icon: "icon-sj", mode: "random" },
        { name: "", icon: "icon-dq", mode: "singleCircle" },
        { name: "", icon: "icon-lb", mode: "listCircle" }
      ]
    };
  },
  computed: {
    ...mapState([
      "showPopup",
      "showModeList",
      "Mode",
      "playList",
      "currentMusicIndex",
      "id"
    ])
  },
  methods: {
    ...mapMutations([
      "handleModeList",
      "handlePopup",
      "changeModeStatus",
      "removePlayListItem",
      "setCurrentMusicIndex",
      "saveDetailId",
      "saveDetail"
    ]),
    // 
    handleMask() {
      this.handlePopup(false);
      this.handleModeList(false);
    },
    // 
    showMode() {
      this.handleModeList(!this.showModeList);
    },
    // 
    changeMode(id) {
      let mode = this.handleMode(id);
      this.changeModeStatus(mode);
      this.handlePopup(false);
    },
    // 
    handleMode(id) {
      switch (id) {
        case 0:
          return "listCircle";
        case 1:
          return "random";
        case 2:
          return "singleCircle";
        case 3:
          return "listCircle";
      }
    },
    //
    // id,
    //route
    //detailvuex,detail
    changeMusic(id, index, item) {
      this.saveDetailId(id);
      // this.setCurrentMusicIndex(index);
      this.saveDetail(item);
      // replacepush,
      this.$router.replace({ name: "detail", query: { id } });
    },
    //
    removeSong(id) {
      this.playList.forEach((item, index) => {
        if (item.sound.id === id) {
          this.removePlayListItem(index);
          return;
        }
      });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.popup {
  position: fixed;
  width: 100%;
  height: 0;
  // height: 100%;
  left: 0;
  bottom: 0;
  font-size: 14px;
  transition: all 0.3s;
  z-index: 221;
  &.active {
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }
  .popup-container {
    position: absolute;
    bottom: 0px;
    left: 0;
    width: 100%;
    height: auto;
    background-color: #fff;
    opacity: 0;
    transition: all 0.5s;
    z-index: 333;

    &.active {
      opacity: 1;
      bottom: 50px;
    }

    .top {
      padding: 5px;
      height: 35px;
      justify-content: space-around;
      color: @shallowGreen;
      .icon-ms {
        font-size: 23px;
      }
    }
    .bottom {
      // 
      height: 200px;
      overflow-y: scroll;

      .content {
        padding: 5px 10px;
        justify-content: space-between;
        height: 40px;
        .left {
          .icon-yinle {
            color: @deepGreen;
            font-size: 16px;
          }
          .name {
            color: #333333;
            margin-left: 3px;
          }
        }
      }
    }

    .mode {
      width: 100%;
      z-index: -1;
      // height: auto;
      flex-direction: column;
      padding: 10px;
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: #fff;
      box-sizing: border-box;
      font-size: 16px;
      border-top: 1px solid #f4f4f4;
      transition: all 0.5s;
      opacity: 0;
      &.active {
        height: auto;
        // bottom: 50px;
        opacity: 1;
        z-index: 330;
      }
      .mode-item {
        width: 100%;
        height: 40px;
        justify-content: space-between;
        color: #757575;
        &.active {
          color: @deepGreen;
        }
        .iconfont {
          font-size: 16px;
        }
      }
    }
  }
}
</style>